import { FETCH_PACKAGE } from '../types';

const INITIAL_STATE: PackageStore = {
	package: {},
};

const PackageReducer = (state = INITIAL_STATE, action: any) => {
	switch (action.type) {
		case FETCH_PACKAGE:
			return {
				...state,
				...action.payload,
			};
		default:
			return state;
	}
};

export default PackageReducer;
