import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import BarChart from './BarChart/BarChart';
import { SizeInfo } from './styled';
import { useDispatch, useSelector } from 'react-redux';
import { fetchVersion } from '../../../../Store/Actions/PackageActions';
import Loader from '../../../../Components/Loader';

const PackageSize: React.FC<PackageSize> = ({ name }) => {
	const { version, selected } = useSelector((state: RootReducer) => state.versionStore);
	const packageName = name ?? '';
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(fetchVersion(packageName));
	}, [dispatch, packageName]);

	// console.log(version, 'version in index');

	if (version.length !== 0) {
		return (
			<Grid container spacing={2} alignItems='center'>
				<Grid container item md={6} xs={12}>
					<Grid item md={12}>
						<SizeInfo>
							<p className='version'>
								Version: <span> {selected.name}</span>
							</p>
						</SizeInfo>
					</Grid>
					<Grid item md={6}>
						<SizeInfo>
							<p className='size-label'>Minified</p>
							<p className='size-info'>
								{selected.unpackedSize} <span>KB</span>
							</p>
						</SizeInfo>
					</Grid>
					<Grid item md={6}>
						<SizeInfo>
							<p className='size-label'>Zipped</p>
							<p className='size-info'>
								{selected.packedSize} <span>KB</span>
							</p>
						</SizeInfo>
					</Grid>
				</Grid>
				<Grid item md={6} xs={12}>
					<BarChart versions={version} />
				</Grid>
			</Grid>
		);
	} else {
		return (
			<Grid container spacing={2} alignItems='center'>
				<Grid item md={12}>
					<div className='w-100 text-center'>
						<Loader />
					</div>
				</Grid>
			</Grid>
		);
	}
};

export default React.memo(PackageSize);
