import React from 'react';
import Grid from '@material-ui/core/Grid';
import { PackageInfoContainer, PackageInfoText } from './styled';
import github from '../../../../assets/images/github.png';
import npm from '../../../../assets/images/npm.png';

const PackageDescription: React.FC<PackageInfo> = (props) => {
	const { name, description, links } = { ...props.packageInfo };

	return (
		<PackageInfoContainer container>
			<Grid container item md={8} xs={12}>
				<Grid item md={12} xs={12}>
					<PackageInfoText>
						<p className='name'>{name}</p>
					</PackageInfoText>
				</Grid>
			</Grid>
			<Grid container item md={4} xs={12}>
				<Grid item md={12} xs={12}>
					<PackageInfoText>
						<ul>
							<li>
								<a href={links?.repository} target='_blank' rel='noopener noreferrer'>
									<img src={github} alt={'github'} />
								</a>
							</li>
							<li>
								<a href={links?.npm} target='_blank' rel='noopener noreferrer'>
									<img src={npm} alt={'npm'} />
								</a>
							</li>
						</ul>
					</PackageInfoText>
				</Grid>
			</Grid>
			<Grid item md={12} xs={12}>
				<PackageInfoText>
					<p className='description'>{description}</p>
				</PackageInfoText>
			</Grid>
		</PackageInfoContainer>
	);
};

export default React.memo(PackageDescription);
